.profileWrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.infoWrapper {
    min-width: 500px;
}

@media screen and (max-width: 600px) {
    .infoWrapper {
        min-width: 0;
        max-width: 500px;
    }
}